function sort(array) {
  //Selection sort
  //function programming

  array.forEach((a, index) => {
    
    let mini = a['distance']; //
    //
    let miniIndex = index;
    let currentIndex = index;
    selectSort(index + 1);
    function selectSort(index) {
      // 1
      if (index === array.length) {
        [array[currentIndex], array[miniIndex]] = [
          array[miniIndex],
          array[currentIndex],
        ];
        return;
      } else {
        if (mini > array[index]['distance']) {
          mini = array[index]['distance'];
          miniIndex = index;
        }
        selectSort(index + 1);
      }
    }
  });
  return array;
}

export default sort;
